export default {
    state:{
        questionAnswer:[
            {
                question : '    ?',
                answers: [
                    ' ',
                    '',
                    ' '
                ]
            },
            {
                question : '  ?',
                answers: [
                    ' 100 000  500 000 .',
                    ' 500 000  1 000 000 .',
                    ' 1 .  10 . .',
                    ' 10 . .'
                ]
            },
            {
                question : '   ,          ?',
                answers: [
                    '  ',
                    '. ',
                    '',
                    ' ',
                    ' ',
                    '  ',
                    ' '
                ]
            }
        ],
        data: [],
        step: 0,
        lastStep: false
    },
    getters: {
        getQuestions(state){
            return state.questionAnswer
        },
        getStep(state){
            return state.step
        },
        getLastStep(state){
            return state.lastStep
        },
        getDataAnswer(state){
            return state.data
        }
    },
    mutations: {
        changeStep(state,payload){
            state.step += payload
        },
        set(state,payload){
            state.data[state.step] = payload
            //console.log(state.data)
        }
    },
    actions: {
        prevNext({state, commit}, value) {
            if (value < 0) {
                commit('changeStep', value)
            } else {
                if (state.step === state.questionAnswer.length - 1) {
                    state.lastStep = !state.lastStep
                    return false
                }
                if (!state.data[state.step]) {
                    alert('  !')
                    return false
                }
                commit('changeStep', value)
            }

        },
        setData({commit}, value) {
            commit('set', value)
        }
    }

};
